html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Headings
----------------------------------------------------------------------------------------------------*/
html body,
html p {
  font-family: "maple-web", sans-serif;
}

h1 {
  line-height: 1.2;
  font-size: 25px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h1 {
  font-family: "maple-web", sans-serif;
}

h2 {
  line-height: 1.2;
  font-size: 23px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h2 {
  font-family: "maple-web", sans-serif;
}

h3 {
  line-height: 1.2;
  font-size: 21px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h3 {
  font-family: "maple-web", sans-serif;
}

h4 {
  line-height: 1.2;
  font-size: 19px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h4 {
  font-family: "maple-web", sans-serif;
}

h5 {
  line-height: 1.2;
  font-size: 17px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h5 {
  font-family: "maple-web", sans-serif;
}

h6 {
  line-height: 1.2;
  font-size: 15px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h6 {
  font-family: "maple-web", sans-serif;
}

/* Header css
 ----------------------------------------------------------------------------------------------------*/
body header#header {
  position: relative;
  background: #000;
}

body header#header #logo.logo-image {
  margin-top: 0;
  margin-bottom: 0;
}

body header#header.menu-below-logo nav.main-nav {
  margin-bottom: 0;
}

body header#header.menu-below-logo nav.main-nav > ul > li {
  font-family: "maple-web", sans-serif;
  font-weight: 700;
}

/* Footer css
 ----------------------------------------------------------------------------------------------------*/
body .section-content p {
  font-style: normal;
}

.main-slider h1 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.main-slider h2 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.main-slider h3 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.main-slider h4 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.main-slider h5 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.main-slider h6 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
}

.base-tile {
  position: relative;
}

.base-tile--shadow:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: #000;
}

.base-tile .wpb_wrapper {
  z-index: 1;
  position: relative;
}

.base-tile h2 {
  color: #fff;
  font-size: 30px;
  font-weight: 300;
}

.base-tile h2 strong {
  color: #fff;
  font-weight: 900;
}

.base-tile h2 a {
  color: #fff;
}

.base-tile h2 a:hover, .base-tile h2 a:active, .base-tile h2 a:focus {
  color: #fff;
}

@media screen and (min-width: 64em) {
  .base-tile h2 {
    font-size: 45px;
  }
}

div.rtb-booking-form {
  margin: 40px 0;
}

div.rtb-booking-form::after {
  clear: both;
  content: "";
  display: table;
}

@media screen and (min-width: 64em) {
  div.rtb-booking-form fieldset {
    float: left;
    width: 48%;
  }
  div.rtb-booking-form fieldset:nth-child(odd) {
    float: right;
  }
}

div.rtb-booking-form legend {
  display: none;
  padding: 0;
  text-transform: uppercase;
}

div.rtb-booking-form label {
  font-weight: 300;
  text-transform: uppercase;
  color: #808080;
}

div.rtb-booking-form form input,
div.rtb-booking-form form select,
div.rtb-booking-form form textarea {
  border-color: #000;
}

div.rtb-booking-form form button[type=submit] {
  margin-top: 30px;
  color: #000;
  border: 1px solid #000;
}

div.rtb-booking-form form button[type=submit]:hover, div.rtb-booking-form form button[type=submit]:active, div.rtb-booking-form form button[type=submit]:focus {
  background: #000;
  color: #fff;
}

